:root {
  --prim-light-cyan: hsl(193, 38%, 86%);
  --prim-neon-green: hsl(150, 100%, 66%);
  --grayish-blue: hsl(217, 19%, 38%);
  --dark-grayish-blue: hsl(217, 19%, 24%);
  --dark-blue: hsl(218, 23%, 16%);
  --ff-manrope: "Manrope", sans-serif;
}

*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  line-height: 1.5;
  background: var(--dark-blue);
  display: grid;
  place-items: center;
  font-family: var(--ff-manrope);
}

.container {
  margin-inline: auto;
  width: min(90%, 75rem);
}

.principal {
  background: var(--dark-grayish-blue);
  border-radius: 10px;
  text-align: center;
  padding-inline: 24px;
  padding-top: 45px;
  display: grid;
  place-items: center;
}
.principal h4 {
  color: var(--prim-neon-green);
  letter-spacing: 4px;
  font-size: 8px;
  font-family: var(--ff-manrope);
  text-transform: uppercase;
  margin-bottom: 32px;
}
.principal .quote {
  font-size: 24px;
  color: var(--prim-light-cyan);
  margin-bottom: 25px;
}
@media (min-width: 440px) {
  .principal .quote {
    margin-bottom: 40px;
  }
}
.principal .divider {
  width: 100%;
  height: 16px;
  background: url(../../images/pattern-divider-mobile.svg) no-repeat;
  background-position: center;
  margin-bottom: 32px;
}
@media (min-width: 440px) {
  .principal .divider {
    background: url(../../images/pattern-divider-desktop.svg) no-repeat;
    background-position: center;
    margin-bottom: 40px;
  }
}
.principal .dado {
  background: var(--prim-neon-green);
  border: none;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  justify-self: center;
  margin-bottom: -32px;
  display: grid;
  place-items: center;
  font-size: 24px;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}
.principal .dado:hover {
  box-shadow: 0px 0px 35px 0px #52ffab;
}
@media (min-width: 440px) {
  .principal {
    max-width: 540px;
    margin: 0 auto;
    padding-inline: 48px;
    padding-top: 52px;
  }
}